@import './styles/_variables.scss';

.app-whrapper {
  position: relative;
  background-color: $mainBgColor;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 100%;
  min-height: calc(100vh - 190px);
  max-width: 1270px;
  display: flex;
  justify-content: space-between;
  padding: 90px 0 100px;
}

.main {
  display: flex;
  width: 100%;
  margin-left: 230px;
}